import React, { createContext, useContext, useEffect, useState } from "react";

const ProductContext = createContext();

export const useProduct = () => useContext(ProductContext);

export const ProductProvider = ({ children }) => {
  const [products, setProducts] = useState([]);
  const [productDetails, setProductDetails] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  //  Gi API t serverless /api/products
  useEffect(() => {
    const fetchProducts = async () => {
      try {
        const res = await fetch("/api/products");
        if (!res.ok) throw new Error("Khng th ti danh sch sn phm");
        const data = await res.json();

        // Nu backend tr v { products, product_detail }
        // th ta tch ra, nu ch tr mng sn phm th vn hot ng
        setProducts(data.products || data || []);
        setProductDetails(data.product_detail || data.products || []);
      } catch (err) {
        console.error("[ProductContext] Fetch error:", err);
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    fetchProducts();
  }, []);

  //  Ly chi tit sn phm theo id (nu  ti ht)
  const getProductDetailById = (id) => {
    return (
      productDetails.find((p) => p.id === id) ||
      products.find((p) => p.id === id)
    );
  };

  //  Ly chi tit sn phm qua API (nu cha c trong context)
  const fetchProductById = async (id) => {
    try {
      const res = await fetch(`/api/products/${id}`);
      if (!res.ok) throw new Error("Khng tm thy sn phm");
      return await res.json();
    } catch (err) {
      console.error("[ProductContext] Fetch product by id error:", err);
      return null;
    }
  };

  return (
    <ProductContext.Provider
      value={{
        products,
        productDetails,
        getProductDetailById,
        fetchProductById,
        loading,
        error,
      }}
    >
      {children}
    </ProductContext.Provider>
  );
};
