"use client"

import React, { useEffect, useState } from "react"
import { CheckCircle, Truck, XCircle, Clock, Download } from "lucide-react"
import "./OrderManager.css"

export default function OrderManager() {
  const [orders, setOrders] = useState([])
  const [filter, setFilter] = useState("Tt c")
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(null)

  useEffect(() => {
    fetchOrders()
  }, [])

  const fetchOrders = async () => {
    try {
      setLoading(true)
      const response = await fetch(`/api/orders`)
      if (!response.ok) throw new Error("Li ti n hng")
      const data = await response.json()
      setOrders(data)
      setError(null)
    } catch (err) {
      console.error("Li:", err)
      setError(err.message)
    } finally {
      setLoading(false)
    }
  }

  const updateStatus = async (id, newStatus) => {
    try {
      const response = await fetch(`/api/orders/${id}`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ status: newStatus }),
      })
      if (!response.ok) throw new Error("Li cp nht trng thi")
      await fetchOrders()
    } catch (err) {
      console.error("Li:", err)
      setError(err.message)
    }
  }

  const filteredOrders = orders.filter((o) => filter === "Tt c" || o.status === filter)

  const formatDate = (iso) => {
    const date = new Date(iso)
    return date.toLocaleString("vi-VN", { hour12: false })
  }

  const getStatusIcon = (status) => {
    switch (status) {
      case " giao":
        return <CheckCircle size={16} className="status-icon delivered" />
      case "ang giao hng":
        return <Truck size={16} className="status-icon delivering" />
      case " hy":
        return <XCircle size={16} className="status-icon cancelled" />
      default:
        return <Clock size={16} className="status-icon pending" />
    }
  }

  if (loading)
    return (
      <div className="order-container">
        <p>ang ti d liu...</p>
      </div>
    )
  if (error)
    return (
      <div className="order-container">
        <p style={{ color: "red" }}>Li: {error}</p>
      </div>
    )

  return (
    <div className="order-container">
      <div className="order-header">
        <div>
          <h1>Qun l n hng</h1>
          <p>Tng cng: {orders.length} n hng</p>
        </div>
        <button className="order-export-btn">
          <Download size={18} />
          Xut d liu
        </button>
      </div>

      <div className="order-tabs">
        {["Tt c", "Ch xc nhn", "ang giao hng", " giao", " thanh ton", " hy"].map((tab) => (
          <button key={tab} className={`tab-btn ${filter === tab ? "active" : ""}`} onClick={() => setFilter(tab)}>
            {tab}
          </button>
        ))}
      </div>

      <div className="order-table-wrapper">
        <table className="order-table">
          <thead>
            <tr>
              <th>M n</th>
              <th>Khch hng</th>
              <th>a ch</th>
              <th>Thanh ton</th>
              <th>Trng thi</th>
              <th>Tng tin</th>
              <th>Ngy to</th>
              <th>Hnh ng</th>
            </tr>
          </thead>
          <tbody>
            {filteredOrders.map((o) => (
              <React.Fragment key={o.id}>
                <tr>
                  <td className="order-id">#{o.id}</td>
                  <td>
                    <div className="customer-info">
                      <div className="customer-name">{o.customer}</div>
                      <div className="customer-email">{o.email}</div>
                    </div>
                  </td>
                  <td>{o.address}</td>
                  <td>{o.payment}</td>
                  <td>
                    <span className={`status-tag ${o.status.toLowerCase().replace(/\s+/g, "-")}`}>
                      {getStatusIcon(o.status)}
                      {o.status}
                    </span>
                  </td>
                  <td className="order-total">{o.total.toLocaleString("vi-VN")} </td>
                  <td className="order-date-pmx">{formatDate(o.createdAt)}</td>
                  <td>
                    <div className="action-group">
                      <button
                        className="action-btn confirm"
                        onClick={() => updateStatus(o.id, " thanh ton")}
                        title="Xc nhn"
                      >
                        <CheckCircle size={16} />
                      </button>
                      <button
                        className="action-btn deliver"
                        onClick={() => updateStatus(o.id, " giao")}
                        title="Giao hng"
                      >
                        <Truck size={16} />
                      </button>
                      <button className="action-btn cancel" onClick={() => updateStatus(o.id, " hy")} title="Hy">
                        <XCircle size={16} />
                      </button>
                    </div>
                  </td>
                </tr>

                <tr className="product-detail-row">
                  <td colSpan="8">
                    <div className="product-detail-box">
                      <h4>Sn phm trong n hng</h4>
                      <table className="item-table">
                        <thead>
                          <tr>
                            <th>nh</th>
                            <th>Tn sn phm</th>
                            <th>Phin bn</th>
                            <th>S lng</th>
                            <th>n gi</th>
                            <th>Tng</th>
                          </tr>
                        </thead>
                        <tbody>
                          {o.items.map((item) => (
                            <tr key={item.productId}>
                              <td>
                                <img
                                  src={item.variantImage || "/fallback.png"}
                                  alt={item.name}
                                  className="item-thumb-small"
                                  onError={(e) => (e.target.src = "/fallback.png")}
                                />
                              </td>
                              <td>{item.name}</td>
                              <td>{item.variant || "Khng c"}</td>
                              <td>{item.quantity}</td>
                              <td>{item.price.toLocaleString("vi-VN")} </td>
                              <td>{(item.quantity * item.price).toLocaleString("vi-VN")} </td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>
                  </td>
                </tr>
              </React.Fragment>
            ))}
          </tbody>
        </table>
      </div>

      <p className="order-note">D liu c lu trn server</p>
    </div>
  )
}
