"use client"
import { X, CheckCircle } from "lucide-react"
import "./FeedbackModal.css"

const FeedbackModal = ({ isOpen, onClose, onNavigate }) => {
  if (!isOpen) return null

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content" onClick={(e) => e.stopPropagation()}>
        <button className="modal-close" onClick={onClose}>
          <X size={24} />
        </button>

        <div className="modal-body">
          <CheckCircle size={64} className="modal-icon" />
          <h2>Cm n bn!</h2>
          <p>nh gi ca bn  c gi thnh cng.</p>
          <p className="modal-subtitle"> kin ca bn rt quan trng v gip chng ti ci thin dch v.</p>
        </div>

        <div className="modal-actions">
          <button className="modal-btn primary" onClick={onNavigate}>
            Tip tc mua sm
          </button>
          <button className="modal-btn secondary" onClick={onClose}>
            ng
          </button>
        </div>
      </div>
    </div>
  )
}

export default FeedbackModal
