"use client"

import { useState, useEffect } from "react"
import { useOrder } from "../../contexts/OrderContext"
import { useProfile } from "../../contexts/ProfileContext"
import { useNavigate } from "react-router-dom"
import { ShoppingBag, Calendar, Package, ArrowRight } from "lucide-react"
import Header from "../../components/Header/Header"
import Footer from "../../components/Footer/Footer"
import { useFeedback } from "../../contexts/FeedbackContext"
import "./MyOrderPage.css"

const MyOrderPage = () => {
  const { orders, fetchOrders, loading: apiLoading, initialLoading } = useOrder()
  const { profile } = useProfile()
  const navigate = useNavigate()
  const [selectedOrder, setSelectedOrder] = useState(null)
  const [error, setError] = useState(null)
  const [showFeedbackModal, setShowFeedbackModal] = useState(false)
  const { checkHasFeedback } = useFeedback()

  useEffect(() => {
    // Ch gi fetchOrders khi  c profile v userId hp l
    if (!initialLoading && profile && fetchOrders) {
      const userId =
        profile?.id ||
        localStorage.getItem("userId") ||
        (profile?.userId ? profile.userId : undefined)
      if (userId) {
        fetchOrders(userId)
          .catch(err => {
            console.error("Li ti n hng:", err);
            setError(err.message || "Khng th ti lch s n hng");
          });
      } else {
        setError("Khng tm thy thng tin ngi dng. Vui lng ng nhp li.");
      }
    }
    if (!initialLoading && !profile) {
      navigate("/auth");
    }
  }, [initialLoading, profile, fetchOrders, navigate])

  useEffect(() => {
    const runCheck = async () => {
      if (profile?.email && orders.length > 0 && checkHasFeedback) {
        try {
          const hasFeedback = await checkHasFeedback(profile.email);
          if (!hasFeedback) {
            setShowFeedbackModal(true)
          }
        } catch (err) {
          console.error("Li kim tra feedback:", err)
        }
      }
    }

    runCheck()
  }, [profile, orders, checkHasFeedback])

  const handleOrderClick = (order) => {
    setSelectedOrder((prevOrder) => (prevOrder?.id === order.id ? null : order))
  }

  if (initialLoading) {
    return (
      <div className="my-order-page">
        <Header />
        <main className="orders-container" style={{ textAlign: 'center', padding: '50px' }}>
          <p>ang ti d liu ngi dng...</p>
          {/* Bn c th thm spinner  y */}
        </main>
        <Footer />
      </div>
    );
  }

  if (apiLoading && orders.length === 0) {
    return (
      <div className="my-order-page">
        <Header />
        <main className="orders-container" style={{ textAlign: 'center', padding: '50px' }}>
          <p>ang ti lch s n hng...</p>
          {/* Bn c th thm spinner  y */}
        </main>
        <Footer />
      </div>
    );
  }

  // Hin th li nu c
  if (error) {
    return (
      <div className="my-order-page">
        <Header />
        <main className="orders-container" style={{ textAlign: 'center', padding: '50px', color: 'red' }}>
          <p>Li: {error}</p>
        </main>
        <Footer />
      </div>
    );
  }

  const getStatusClass = (status) => {
    if (!status) return "status-badge"
    const s = status.toLowerCase()
    if (s.includes("ch xc nhn")) return "status-badge cho-xac-nhan"
    if (s.includes("ang giao")) return "status-badge dang-giao-hang"
    if (s.includes("shipping")) return "status-badge shipping"
    if (s.includes("delivered") || s.includes(" giao")) return "status-badge da-giao"
    return "status-badge"
  }

  return (
    <div className="my-order-page">
      <Header />

      <main className="orders-container">
        <div className="orders-header">
          <h1>n hng ca ti</h1>
          <p className="orders-count">Tng cng: {orders.length} n hng</p>
        </div>
        {showFeedbackModal && (
          <div className="feedback-reminder-banner">
            <div className="feedback-reminder-content">
              <span>
                Bn cha gi nh gi cho tri nghim mua hng. Hy{" "}
                <a href="/feedback" className="feedback-reminder-link">
                  gi nh gi
                </a>{" "}
                 gip <span className="logo-b-highlight">Blinko</span> ci thin dch v!
              </span>
              <button
                className="feedback-reminder-close"
                onClick={() => setShowFeedbackModal(false)}
                title="ng"
              >
                
              </button>
            </div>
          </div>
        )}
        {orders.length === 0 && !apiLoading ? (
          <div className="empty-orders">
            <ShoppingBag size={64} />
            <h2>Cha c n hng no</h2>
            <p>Hy mua sm ngay  to n hng u tin ca bn</p>
            <button className="shop-btn" onClick={() => navigate("/products")}>
              Tip tc mua sm
            </button>
          </div>
        ) : (
          <div className="orders-content">
            <div className="orders-list">
              {orders.map((order) => (
                <div
                  key={order.id}
                  className={`order-card ${selectedOrder?.id === order.id ? "active" : ""}`}
                  onClick={() => handleOrderClick(order)}
                >
                  <div className="order-card-header">
                    <div className="order-info">
                      <h3>n hng #{order.id}</h3>
                      <p className="order-date">
                        <Calendar size={14} />
                        {new Date(order.createdAt || order.date).toLocaleDateString("vi-VN")}
                      </p>
                    </div>
                    <div className="order-status">
                      <span className={getStatusClass(order.status)}>{order.status}</span>
                    </div>
                  </div>
                  <div className="order-card-footer">
                    <p className="order-total">Tng cng: {order.total?.toLocaleString("vi-VN") || "0"} </p>
                    <p className="order-items">
                      <Package size={14} />
                      {order.items?.length || 0} sn phm
                    </p>
                  </div>
                </div>
              ))}
            </div>

            {!selectedOrder ? (
              <div className="order-detail-empty">
                <ShoppingBag size={64} />
                <h2>Chn mt n hng</h2>
                <p>Nhp vo mt n hng t danh sch bn tri  xem chi tit</p>
              </div>
            ) : (
              <div className="order-detail-container">
                <div className="order-detail">
                  <div className="detail-header">
                    <h2>Chi tit n hng #{selectedOrder.id}</h2>
                    <p className="detail-date">
                      {new Date(selectedOrder.createdAt || selectedOrder.date).toLocaleDateString("vi-VN", {
                        weekday: "long",
                        year: "numeric",
                        month: "long",
                        day: "numeric",
                      })}
                    </p>
                  </div>

                  <div className="detail-recipient">
                    <h3>Thng tin ngi nhn</h3>
                    <p>
                      <strong>Tn:</strong> {selectedOrder.customer || selectedOrder.recipientName || "Khng r"}
                    </p>
                    <p>
                      <strong>S in thoi:</strong> {selectedOrder.phone || "Khng r"}
                    </p>
                    <p>
                      <strong>Email:</strong> {selectedOrder.email || "Khng r"}
                    </p>
                    <p>
                      <strong>a ch:</strong> {selectedOrder.address || "Khng r"}
                    </p>
                    {selectedOrder.note && (
                      <p>
                        <strong>Ghi ch:</strong> {selectedOrder.note}
                      </p>
                    )}
                  </div>

                  <div className="detail-items">
                    <h3>Sn phm</h3>
                    <div className="items-list">
                      {selectedOrder.items.map((item, index) => (
                        <div key={`${selectedOrder.id}-${item.productId || item.id}-${index}`} className="detail-item">
                          <img src={item.variantImage || item.image} alt={item.name} className="item-image" />
                          <div className="item-info">
                            <h4>{item.name}</h4>
                            {(item.variant || item.variantName) && (
                              <p className="item-variant">Phn loi: {item.variant || item.variantName}</p>
                            )}
                            <p className="item-sku">SKU: {item.productId || item.id}</p>
                          </div>
                          <div className="item-quantity">
                            <span>x{item.quantity}</span>
                          </div>
                          <div className="item-price">{(item.price * item.quantity).toLocaleString("vi-VN")} </div>
                        </div>
                      ))}
                    </div>
                  </div>

                  <div className="detail-summary">
                    <div className="summary-row">
                      <span>Tng tin hng:</span>
                      <span>{selectedOrder.total?.toLocaleString("vi-VN") || "0"} </span>
                    </div>
                    <div className="summary-row">
                      <span>Ph vn chuyn:</span>
                      <span>Min ph</span>
                    </div>
                    <div className="summary-row total">
                      <span>Tng cng:</span>
                      <span>{selectedOrder.total?.toLocaleString("vi-VN") || "0"} </span>
                    </div>
                  </div>

                  <div className="detail-actions">
                    <button className="action-btn primary" onClick={() => navigate("/products")}>
                      <ShoppingBag size={18} />
                      Tip tc mua sm
                    </button>
                    <button className="action-btn secondary" onClick={() => setSelectedOrder(null)}>
                      <ArrowRight size={18} />
                      Quay li
                    </button>
                  </div>
                </div>
              </div>
            )}
          </div>
        )}
      </main>
      <Footer />
    </div>
  )
}

export default MyOrderPage
